<template>
  <div>
    <v-row justify="center" class="my-5">
      <v-btn color="primary" :to="{ name: 'MyRecord', params: { id: this.id } }"
        ></v-btn
      >
    </v-row>
    <v-row justify="center" class="my-5">
      <div>{{ workout.workout }}</div>
    </v-row>
    <v-card outlined class="mx-2">
      <v-container>
        <h2 class="text-center">
          <p class="mb-5">{{ workout.workout }}</p>
        </h2>
        <p>{{ workout.part_of_body }}</p>
        <p>{{ workout.memo }}</p>
      </v-container>
    </v-card>
    <v-row justify="center">
      <v-btn color="primary" :to="{ name: 'home' }" text class="mx-2 my-5"
        ></v-btn
      >
      <v-btn
        color="primary"
        :to="{ name: 'MyEditor', params: { id: this.id } }"
        text
        class="mx-2 my-5"
        ></v-btn
      >
      <v-btn color="error" @click="deleteWorkoutData" class="my-5"></v-btn>
    </v-row>
  </div>
</template>

<script>
import { apiService } from "../common/api.service.js";

export default {
  name: "MyWorkout",
  props: {
    id: {
      type: Number,
      required: true,
    },
  },
  data() {
    return {
      workout: {},
      body_items: ["", "", "", "", "", ""],
    };
  },
  methods: {
    setPageTitle(title) {
      document.title = title;
    },
    getWorkoutData() {
      let endpoint = `/api/workouts/${this.id}/`;
      apiService(endpoint).then((data) => {
        this.workout = data;
        this.setPageTitle(data.workout);
      });
    },
    deleteWorkoutData() {
      let endpoint = `/api/workouts/${this.id}/`;
      apiService(endpoint, "DELETE").then(() => {
        this.$router.push({
          name: "home",
        });
      });
    },
  },
  created() {
    this.getWorkoutData();
    // console.log(location.href);
  },
};
</script>

